.mapContainer {
    max-width: 800px;
    display: flex;
    flex-wrap: wrap;
  }
  .mountain {
    width: 100px;
    height: 100px;
    background-color: grey;
  }
  .resource {
    width: 100px;
    height: 100px;
    background-color: green;
  }
  .special {
    width: 100px;
    height: 100px;
    background-color: yellow;
  }
  .button {
    background-color: purple !important;
    color: white !important;
  }
  .buttonHolder {
    display: flex;
    width: 100%;
    padding: 12px;
    justify-content: space-between;
    color: white;
    font-size: 30px;
    font-weight: bold;
  }

  @media (max-width: 800px)
  {
    .mapContainer {
        max-width: 400px;
        display: flex;
        flex-wrap: wrap;
      }
      .mountain {
        width: 50px;
        height: 50px;
        background-color: grey;
      }
      .resource {
        width: 50px;
        height: 50px;
        background-color: green;
      }
      .special {
        width: 50px;
        height: 50px;
        background-color: yellow;
      }
      .buttonHolder {
        display: flex;
        flex-direction: column;
        width: 100%;
        padding: 12px;
        justify-content: space-between;
        color: white;
        font-size: 20px;
        font-weight: bold;
      }
  }

  @media (max-width: 400px)
  {
    .mapContainer {
        max-width: 200px;
        display: flex;
        flex-wrap: wrap;
      }
      .mountain {
        width: 25px;
        height: 25px;
        background-color: grey;
      }
      .resource {
        width: 25px;
        height: 25px;
        background-color: green;
      }
      .special {
        width: 25px;
        height: 25px;
        background-color: yellow;
      }
      .buttonHolder {
        display: flex;
        flex-direction: column;
        width: 100%;
        padding: 12px;
        justify-content: space-between;
        color: white;
        font-size: 15px;
        font-weight: bold;
      }
  }